<script>
import axios from 'axios';


export default {
  data() {
    return {
      countdown: {
        days: '00',
        hours: '00',
        minutes: '00',
        seconds: '00'
      },
      form: {
        name: '',
        email: '',
        celebrationParts: '',
        food: '',
        notes: '',
      },
    };
  },
  mounted() {
    this.startCountdown('2025-09-19T14:00:00+03:00');
  },
  beforeUnmount() {
    clearInterval(this.countdownInterval);
  },
  methods: {
    /* global Toastify, bootstrap */


    async submitRSVP() {
      try {
        const payload = {
          name: this.form.name,
          email: this.form.email,
          celebrationParts: this.form.celebrationParts,
          food: this.form.food,
          notes: this.form.notes,
        };

        await axios.post('https://api-kb43n3zbtq-uc.a.run.app/rsvp', payload);

        this.form = {
          name: '',
          email: '',
          celebrationParts: '',
          food: '',
          notes: '',
        };

        Toastify({
          text: "RSVP sėkmingai išsiųsta!",
          duration: 3000,
          close: true,
          gravity: "top",
          position: "center",
          backgroundColor: "#4CAF50",
        }).showToast();

        const modal = document.getElementById('rsvp-modal');
        const bootstrapModal = bootstrap.Modal.getInstance(modal) || new bootstrap.Modal(modal);
        bootstrapModal.hide();
      } catch (error) {
        console.error('RSVP submission failed:', error);
        alert('Kažkas ne taip, pamėginkite vėliau');
      }
    },

    startCountdown(targetDate) {
      const end = new Date(targetDate).getTime();

      this.countdownInterval = setInterval(() => {
        const now = new Date().getTime();
        const distance = end - now;

        if (distance < 0) {
          clearInterval(this.countdownInterval);
          this.countdown = {
            days: '00',
            hours: '00',
            minutes: '00',
            seconds: '00'
          };
          return;
        }

        const days = Math.floor(distance / (1000 * 60 * 60 * 24));
        const hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        const seconds = Math.floor((distance % (1000 * 60)) / 1000);

        this.countdown = {
          days: String(days).padStart(2, '0'),
          hours: String(hours).padStart(2, '0'),
          minutes: String(minutes).padStart(2, '0'),
          seconds: String(seconds).padStart(2, '0')
        };
      }, 1000);
    }
  }
};
</script>

